import {Button, Segment, Grid, Icon, Header} from 'semantic-ui-react';
import React, { Component } from 'react';
import moment from 'moment';


export default class DeviceGridColumn extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Grid.Column>
                  <Segment placeholder>
                    <Segment.Inline>
                        <Header as='h2'>{this.props.name}</Header>
                        {this.props.file ?
                                <Icon color='green' name='check'/>
                            :
                                <Icon color='red' name='close'/>
                        }
                    </Segment.Inline>
                        <Segment>{this.props.name}</Segment>
                        <Segment>Middle</Segment>
                        <Segment>
                            {this.props.file ?
                                <Icon color='green' name='check'/>
                            :
                                <Icon color='red' name='close'/>
                            }
                        </Segment>
                    </Segment>
            </Grid.Column>
        )
    }
}

